


































































@import "../styles/colors";

.todo {
  flex: 1;
  padding: 1.25rem 0;
}

.todo-title {
  color: $white;
}

.todo-add {
  display: flex;

  &__button {
    margin-left: 0.75rem;
  }

  &__controls {
    display: flex;
    margin-left: auto;
  }
}

.todo-container {
  background-color: $white;
  border: 2px solid $blue;
  border-radius: 1.25rem;
  margin-bottom: 1.75rem;
}

.todo-empty {
  padding: 1.25rem;
}
