












































@import "../styles/colors";

.login {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;

  &__title {
    color: $white;
    margin: 0 0 2.5rem;
  }

  &__button {
    margin-top: 0.25rem;
    width: 100%;
  }
}

form {
  max-width: 20rem;
}

label {
  display: block;
  margin-bottom: 0.25rem;
}

input {
  margin-bottom: 1.25rem;
  width: 100%;
}
